          figcaption Fuente: SENA, (2021) 
      .row.justify-content-center(titulo="Paso 3. Trazo de la espalda ")
        .col-lg-7
          h5 A. Base del patrn 
          ul.lista-ul--color.ico-lista.fa-lista.mb-4
            li 
              i.fas.fa-angle-right
              span Iniciar con el patrn delantero infantil bsico, con sus proporciones de escote, hombro, sisa y lnea de costado.    
          h5 B. Escote 
