            li 
              i.fas.fa-angle-right
              span Marcar la lnea de centro dividiendo el rectngulo en dos partes iguales. 
            li 
              i.fas.fa-angle-right
              span Esta lnea servir como referencia para dar forma a la curva superior del cuello. 
          h5 C. Altura superior 
          ul.lista-ul--color.ico-lista.fa-lista.mb-0
            li 
              i.fas.fa-angle-right
              span Desde la lnea de base hacia arriba, marcar #[strong 7 cm] en el centro. 
            li 
              i.fas.fa-angle-right
              span Esta medida define la elevacin mxima del cuello. 
          h5 D. Altura inferior
          ul.lista-ul--color.ico-lista.fa-lista.mb-0
            li 
              i.fas.fa-angle-right
              span Desde la lnea de base hacia arriba, marcar #[strong 6 cm] en el centro. 
            li 
              i.fas.fa-angle-right
              span Esta lnea define la cada o parte ms baja del cuello. 
          h5 E. Entalles laterales 
          ul.lista-ul--color.ico-lista.fa-lista.mb-0
            li 
              i.fas.fa-angle-right
              span En ambos extremos superiores del rectngulo, subir #[strong 0,7 cm] y trazar una curva suave hacia el centro. 
            li 
              i.fas.fa-angle-right
              span En los extremos inferiores, entrar #[strong 1 cm] hacia adentro para dar ajuste a la forma del cuello.                                                                                                               
          h5 F. Curvatura final 
          ul.lista-ul--color.ico-lista.fa-lista.mb-0
            li 
              i.fas.fa-angle-right
              span Unir los puntos laterales (0,7 cm) con el centro del cuello, creando la lnea superior curva. 
            li 
              i.fas.fa-angle-right
              span Unir los puntos de entrada (1 cm en cada lado) hacia el centro de la base para definir la parte inferior. 
        .col-lg-4
          .bg-color-3.p-3
            .titulo-sexto.color-acento-contenido.mb-3
              h5 Figura 6. 
              span Trazo lateral del cuello marinero            
            figure
              img.py-lg-5(src="@/assets/curso/temas/fig6.svg" , alt='Se presenta el trazo lateral del cuello marinero compuesto por una base recta, una lnea central vertical que divide el patrn en dos partes y dos elevaciones superiores de 7 cm en el centro. En ambos extremos superiores se marca un punto a 0,7 cm para formar una curva suave hacia el centro. En la parte inferior se registra una cada de 6 cm, tambin medida desde la lnea central. Los bordes laterales se inclinan ligeramente hacia adentro para ajustar la forma del cuello.')
