    LineaTiempoD.color-acento-botones(data-aos="fade-down")
      .row.p-lg-4(numero="1" titulo="Paso 1. Trazo del delantero")
        p Se inicia con el patrn base del cuerpo delantero, marcando el centro delantero como lnea de referencia. Sobre este se definen las medidas principales (hombro, busto y cintura) y se procede a suavizar la lnea del escote, que posteriormente se adaptar para incorporar el caracterstico cuello beb. Este primer trazo constituye la base sobre la cual se realizarn las siguientes transformaciones de diseo.  
        .row.justify-content-center
          .col-lg-4.mb-4.mb-lg-0
            .bg-color-3.p-3
              .titulo-sexto.color-acento-contenido.mb-3
                h5 Figura 8. 
