                img.img-a.img-t(src="@/assets/curso/temas/fig8.svg" , alt='Molde tcnico del delantero de una blusa infantil con indicaciones de medidas para el bolsillo aplicado, la sisa, el cruce de botonadura y referencias de largo, sealadas en centmetros para orientar el trazado y confeccin de la prenda.')
