                    th Trazado
                tbody
                  tr.bgt-1
                    td.text-bold.text-center 1
                    td.text-start Lnea de abotonadura = 1,5 cm.
                    td.text-start Ampliar desde el centro delantero hacia el costado.
                  tr.bgt-2
                    td.text-bold.text-center 2
                    td.text-start Separacin de botones (3 a 4 puntos).
                    td.text-start Marcar los puntos de colocacin de botones a lo largo de la vista.
                  tr.bgt-1
                    td.text-bold.text-center 3
                    td.text-start Profundidad de sisa = 1,5 cm.
                    td.text-start Bajar en el costado desde la curva de la sisa.
                  tr.bgt-2
                    td.text-bold.text-center 4
                    td.text-start Bolsillo aplicado = 7,5 * 7,5 cm.
                    td.text-start Ubicar a 3,5 cm del centro delantero y a 7,5 cm del escote.
                  tr.bgt-1
                    td.text-bold.text-center 5
                    td.text-start Largo de referencia = 15 cm.
                    td.text-start Marcar desde la lnea inferior hacia arriba.                         
      .row.p-lg-4(numero="2" titulo="Paso 2. Trazo del patrn de la espalda")
        p Se toma como base el molde posterior del cuerpo, marcando el #[strong centro de la espalda] y trasladando las medidas correspondientes al ancho de hombros, sisa y largo de la prenda. Es importante mantener la proporcin con el delantero para garantizar el calce adecuado. El escote posterior se dibuja ligeramente ms alto y cerrado que el delantero, lo cual permite un mejor ajuste y comodidad en la zona del cuello. 
        .row.justify-content-center
          .col-lg-4.mb-4.mb-lg-0
              .bg-color-3.p-3
