                  tr.bgt-2
                    td.text-bold.text-center 2
                    td.text-start Centro de la copa = 10 cm.
                    td.text-start Medir 10 cm en la parte superior del rectngulo para definir el centro de la copa de la manga.
                  tr.bgt-1
                    td.text-bold.text-center 3
                    td.text-start Altura de frunce = 1,5 cm.
                    td.text-start Marcar 1,5 cm en la lnea inferior de la manga para generar ligera curva ascendente.
                  tr.bgt-2
                    td.text-bold.text-center 4
                    td.text-start Curva de la copa.
                    td.text-start Dibujar la curva de la copa uniendo los puntos laterales con el centro marcado en 10 cm.
                  tr.bgt-1
                    td.text-bold.text-center 5
                    td.text-start Curvatura inferior.
                    td.text-start Desde los 1,5 cm sealados, redibujar la lnea de bajo de la manga con ligera curva.
                  tr.bgt-2
                    td.text-bold.text-center 6
                    td.text-start Revisin final.
                    td.text-start Confirmar la simetra entre ambos lados de la manga y que la longitud del bajo coincida con el contorno de sisa.                        
      .row.p-lg-4(numero="4" titulo="Paso 4. Trazo del cuello beb ")
        .row.justify-content-center
          .col-lg-6
            p Sobre el escote del delantero y la espalda ya trazados, se procede a delimitar la forma caracterstica del #[strong cuello beb], marcando primero la base de apoyo y luego el contorno redondeado que lo distingue. El molde se desarrolla de manera simtrica para asegurar equilibrio en ambos lados de la prenda.               
