                    td.text-bold.text-center 1
                    td.text-start Escote modificado.
                    td.text-start Redibujar el escote delantero segn el diseo, ampliando la curva para recibir el cuello.
                  tr.bgt-2
                    td.text-bold.text-center 2
                    td.text-start Medida de cuello = 9 cm.
                    td.text-start Desde el escote delantero y trasero marcar 9 cm hacia el interior para definir la base del cuello.
                  tr.bgt-1
                    td.text-bold.text-center 3
                    td.text-start Altura del cuello = 1,5 cm. 
                    td.text-start Levantar 1,5 cm desde la base del cuello hacia arriba. 
                  tr.bgt-2
                    td.text-bold.text-center 4
                    td.text-start Curva superior del cuello.
                    td.text-start Unir los puntos marcados con una lnea curva suave que forme el contorno externo del cuello beb.
                  tr.bgt-1
                    td.text-bold.text-center 5
                    td.text-start Desarrollo del patrn del cuello.
                    td.text-start Separar la pieza resultante (independizar la forma del cuello) para su confeccin como pieza aparte.
                  tr.bgt-2
                    td.text-bold.text-center 6
                    td.text-start Revisin final.
                    td.text-start Es necesario confirmar que la medida del cuello coincida con la abertura del escote y que las lneas sean simtricas.                        


